import api from '@/modules/api'
import appG from '@/modules/appGlobal'

export default {
  data() {
    return {
      //是否是选择收货地址
      isSelect: false,
      //默认收货地址框
      popShow: false,
      //收货地址列表
      addressList: [],
      //设置默认时的临时收货地址
      tmpAddress: {},
      //是否显示设置默认收货地址
      isCanDefault: true
    }
  },
  onLoad(opt) {
    if (opt.isSelect) {
      this.isSelect = true
    }
    this.api_308()
  },
  methods: {
    /**
     * 获取收货地址列表
     */
    api_308: function () {
      let that = this
      api.post(api.api_308, api.getSign(),
        function (app, res) {
          if (res.data.Basis.State == api.state.state_200) {
            that.addressList = res.data.Result
            that.addressList.forEach((ele, index) => {
              if (ele.is_default) {
                that.tmpAddress = ele
              }
            })
          }
        })
    },
    /**
     * 设置默认收货地址
     */
    api_310: function (id) {
      let that = this
      api.post(api.api_310, api.getSign({ ID: id }),
        function (app, res) {
          if (res.data.Basis.State == api.state.state_200) {
            uni.showToast({ title: res.data.Basis.Msg, duration: 2000 })
            that.popShow = false
            //设置其余收货地址非默认
            that.addressList.forEach((ele, index) => {
              //   if (ele.id != id) {
              //     that.$set(ele, 'is_default', false)
              //   } else {
              //     that.$set(ele, 'is_default', true)
              //   }
              if (ele.id != id) {
                ele.is_default = false
              } else {
                ele.is_default = true
              }
            })
          } else {
            uni.showToast({ title: res.data.Basis.Msg, duration: 2000, icon: 'none' })
          }
        })
    },
    /**
     * 删除收货地址
     */
    api_311: function (id) {
      let that = this
      api.post(api.api_310, api.getSign({ ID: id }),
        function (app, res) {
          if (res.data.Basis.State == api.state.state_200) {

          }
        })
    },
    /**
     * 隐藏手机号码中间4位
     */
    getHideMobile: function (mobile) {
      return appG.util.getHideMobile(mobile)
    },
    /**
     * 弹出设置默认收货地址框
     */
    setPopShow: function (item) {
      let that = this
      if (that.isSelect) {
        uni.navigateTo({
          url: "../address/manage?said=" + item.id
        })
      } else {
        //如果是默认
        this.isCanDefault = !item.is_default
        //   if (item.is_default) {
        //     this.addressList.forEach((ele, index) => {
        //       if (ele.id == item.id) {
        //         that.$set(ele, 'is_default', true)
        //         that.is_default = true
        //       }
        //     })
        //   }
        //反向设置默认值
        item.is_default = !item.is_default
        this.popShow = true
        this.tmpAddress = item
      }
    },
    /**
     * 关闭弹出框
     */
    cancelPopShow: function () {
      let that = this
      that.popShow = false
      if (that.tmpAddress.id) {
        that.addressList.forEach((ele, index) => {
          if (ele.id == that.tmpAddress.id) {
            ele.is_default = false
          }
        })
        that.tmpAddress = {}
      }
    },
    /**
     * 设置默认收货地址
     */
    setDefault: function () {
      let that = this
      that.api_310(this.tmpAddress.id)
    },
    /**
     * 编辑收货地址
     */
    editAddress: function () {
      uni.navigateTo({
        url: "form?id=" + this.tmpAddress.id
      })
    },
    /**
     * 删除收货地址
     */
    delAddress: function () {
      let that = this
      that.api_310(this.tmpAddress.id)
    }

  }
}
